.search-div {
  z-index: 1100;
  min-height: 64px;
  color: #fff;
  background-color: #3f51b5;
  justify-content: center;
  align-items: center;
  padding: 12px 24px;
  display: flex;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  left: 0;
  box-shadow: 0 2px 4px -1px rgba(0, 0, 0, .2), 0 4px 5px rgba(0, 0, 0, .14), 0 1px 10px rgba(0, 0, 0, .12);
}

.search-form {
  width: 100%;
  max-width: 600px;
  background-color: #fff;
  border-radius: 3px;
  align-items: center;
  display: flex;
  overflow: hidden;
}

#submit {
  width: 48px;
  height: 48px;
  opacity: .6;
  cursor: pointer;
  background-position: center;
  background-repeat: no-repeat;
  background-size: 40%;
  border: 0;
  outline: none;
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1);
  display: inline-block;
}

#submit:hover {
  opacity: 1;
}

.input {
  width: 100%;
  font: inherit;
  border: none;
  outline: none;
  padding-left: 4px;
  padding-right: 4px;
  font-size: 20px;
  display: inline-block;
}

.input::-ms-input-placeholder {
  font: inherit;
  font-size: 18px;
}

.input::placeholder {
  font: inherit;
  font-size: 18px;
}

.gallery {
  max-width: calc(100vw - 48px);
  grid-gap: 16px;
  grid-template-columns: repeat(auto-fill, minmax(320px, 1fr));
  margin: 0 auto;
  padding: 0;
  list-style: none;
  display: grid;
}

.ImageGalleryItem {
  border-radius: 2px;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.ImageGalleryItemImage {
  width: 100%;
  height: 260px;
  object-fit: cover;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
}

.ImageGalleryItemImage:hover {
  cursor: zoom-in;
  transform: scale(1.03);
}

.info {
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 15px;
  display: flex;
}

.info-item {
  flex-direction: column;
  gap: 5px;
  display: flex;
}

